.homeContainer
{
    width: 100%;
    height: 110vh;
    background-image: linear-gradient(rgba(0,0,0,.5),rgba(0,0,0,.5)),url('../../../img/hero-area.jpg');
    background-size: 100% 100%;
    position: relative;
}
.homeContainerText
{
    width: 60%;
    height: 65vh;
    /* border: 1px solid red; */
    position: absolute;
    left: 23%;
    top:15%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    line-height:55px;
}
.thisIS
{
    font-family: "Shadows Into Light", cursive;
    font-size: 50px;
    color: white;
}
.homeContainerText>h1
{
    font-family:"Source Sans Pro", sans-serif;
    font-size: 40px;
    color: white;
    letter-spacing: 10px;
}
.position
{
    font-family: "Shadows Into Light", cursive;
    font-size: 30px;
    color: white;
}
.iconsDiv
{
    width: 40%;
    height: 10vh;
    /* border: 1px solid red; */
    display: flex;
    justify-content:space-evenly;
    align-items: center;
    position: relative;
    top:5%;
}

.iconsDiv>div
{
    width: 13%;
    height: 7vh;
    background: white;
    border-radius: 100%;
    display: flex;
    justify-content:center;
    align-items: center;
    transition: .3s;
}
.iconsDiv>div:nth-child(1):hover
{
    background: #3b5999;
}
.iconsDiv>div:nth-child(2):hover
{
    background: #4a9cec;
}
.iconsDiv>div:nth-child(3):hover
{
    background: #d6274d;
}
.iconsDiv>div:nth-child(4):hover
{
    background: #1260a2;
}
.iconsDiv>div:nth-child(5):hover
{
    background: #ce332a;
}
.homeContainerText>button
{
    width: 25%;
    height: 8vh;
    background: #00b4d9;
    font-family: "Source Sans Pro", sans-serif;
    font-size: 14px;
    color: white;
    position: relative;
    top:13%;
    border: none;
    outline: none;
    border-radius: 5px 5px 5px 5px;
}
.homeContainerText>button:hover
{
    background: #21cff3;
}